/** @jsx jsx */
import { jsx } from "theme-ui";

import { graphql } from "gatsby";
import { mapCraft } from "./mappings";

import { useEffect, useState } from "react";
import InstantSearch from "@components/InstantSearch";

const Impacts = (props) => {
  const { category } = mapCraft(props.data.craft);

  const { title } = category;

  useEffect(() => {
    if (title) {
      setRefinements({
        ...defaultRefinements,
        populationGroups: [title],
      });
    }
  }, []);

  const [defaultRefinements, setRefinements] = useState({
    type: [],
    populationGroups: [],
  });

  return (
    <div>
      <InstantSearch
        refinementType="populationGroups"
        defaultRefinement={defaultRefinements.populationGroups}
        isSearch={false}
        heading={title}
      />
    </div>
  );
};

export default Impacts;

export const query = graphql`
  query PopulationGroupsQuery($id: Craft_QueryArgument) {
    craft {
      category(id: [$id]) {
        ... on Craft_populationGroups_Category {
          title
        }
      }
    }
  }
`;
